@media (max-width: 1920px) {
  *::-webkit-scrollbar {
    display: none;
  }
  body::-webkit-scrollbar {
    display: none;
    box-sizing: border-box;
  }
  .App::-webkit-scrollbar {
    display: none;
  }
  .maincontent::-webkit-scrollbar {
    display: none;
  }
  body {
    overflow: scroll;
    cursor: default;
  }
  .App {
    height: 100vh;
    background: radial-gradient(circle at top left, #0d0d0d, #1a1a1a, #101010);
    display: flex;
    overflow: scroll;
  }
  .maincontent {
    position: fixed;
    left: 450px;
    height: 100vh;
    overflow: scroll;
    width: 1050px;
    scrollbar-width: 0px;
  }
  .header-block {
    background-color: #35a07c;
    padding: 0px 5px;
    border-radius: 10px;
    text-align: center;
    align-items: center;
    display: inline-flex;
  }
  .header-block > img {
    width: 20px;
    height: 20px;
  }
  .namecolor {
    color: #35a07c;
  }
  .fontbold {
    font-weight: 600;
  }
}

@media (max-width: 420px) {
  .App {
    max-width: 100%;
  }
  .maincontent {
    left: 0px;
    z-index: 0;
    width: 100%;
  }
  .maincontent-components {
    padding: 0px 10px;
  }
  .introproject {
    font-size: 20px;
  }
}
@media (min-width: 420px) and (max-width: 1024px) {
  .App {
    max-width: 100%;
  }
  .maincontent {
    left: 0px;
    z-index: 0;
    width: 100%;
  }
  .maincontent-components {
    padding: 0px 10px;
  }
}
