@media (max-width: 1920px) {
  .introduction {
    margin-top: 80px;
  }
  .introtextanimation {
    display: inline-block;
    font-size: 50px;
    white-space: nowrap;
    overflow: hidden;
    color: white;
  }

  .specialty {
    color: #35a07c;
    animation: changeSpecialty 4s infinite;
  }
  .introabout {
    color: white;
    max-width: 1000px;
  }
  .resumecontainer {
    display: flex;
    box-sizing: border-box;
    cursor: pointer;
  }
  .emptyspace {
    width: 700px;
  }
  .imagewrapper {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .downloadcircle {
    width: 210px;
    height: 210px;
    display: flex;
    justify-content: center;
    align-items: center;
    animation: rotate 9s linear infinite;
  }
  .downloadimage {
    position: absolute;
  }
  .introproject {
    font-size: 25px;
    color: white;
  }
}
@keyframes changeSpecialty {
  0%,
  50% {
    width: 350px;
  }
  55%,
  100% {
    width: 0;
  }
}
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@media (max-width: 420px) {
  .introtextanimation {
    font-size: 35px;
    max-width: 95%;
    white-space: normal;
    height: 140px;
  }
  .introabout {
    font-size: 15px;
  }
  .downloadcircle {
    width: 150px;
    height: 150px;
  }
  .introproject {
    font-size: 20px;
  }
}
@media (min-width: 420px) and (max-width: 1024px) {
  .introtextanimation {
    font-size: 35px;
    max-width: 90%;
    white-space: normal;
    height: 90px;
  }
  .introabout {
    font-size: 15px;
  }
  .downloadcircle {
    width: 150px;
    height: 150px;
  }
  .introproject {
    font-size: 20px;
  }
}
