@media (max-width: 1920px) {
  .education {
    width: 900px;
  }
  .EducationDetails {
    padding: 10px;
    color: white;
  }
  .EducationDetails-head {
    font-size: 20px;
    font-weight: bold;
  }
  .EducationDetails-content {
    display: flex;
    font-size: 25px;
  }
  .EducationDetails-content > div {
    text-align: center;
    flex: 1;
  }
  .Companyname {
    align-items: center;
    box-sizing: border-box;
  }
  .inner {
    opacity: 0;
    transition: opacity 1.5s, transform 1.5s;
    transform: translateY(100%);
  }
  .slide-up {
    opacity: 1;
    transform: translateY(0);
  }
}
@media (max-width: 1024px) {
  .education {
    width: 95%;
  }
  .EducationDetails-head {
    font-size: 15px;
  }
  .EducationDetails-content {
    font-size: 15px;
    justify-content: space-between;
  }
}
@media (min-width: 420px) and (max-width: 1024px) {
}
