@media (max-width: 1920px) {
  .sidebar {
    width: 400px;
    height: 100%;
    background: none;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    position: fixed;
  }
  .sidebar-uppercontent {
    display: flex;
    justify-content: space-between;
  }
  .sidebar-content {
    border: 1px solid #6e6e6e;
    border-radius: 20px;
    height: 90%;
    width: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: scroll;
  }
  .sidebar-content::-webkit-scrollbar {
    display: none;
  }
  .sidebar-content > div {
    text-align: center;
  }
  .sidebar-content > div > div {
    margin: 20px auto;
  }
  .profileimg {
    margin: auto;
  }
  .profileimg > img {
    height: 290px;
    width: 280px;
    border-radius: 10px;
    box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.2);
  }
  .sidebar-name {
    color: white;
    font-size: 30px;
    text-align: left;
  }
  .sidebar-specialities {
    font-size: 15px;
    text-align: right;
  }
  .sidebar-otherdetails {
    color: #ffffff;
    font-size: 20px;
    text-align: left;
  }
  .sidebar-otherdetails > div > img {
    width: 25px;
    height: 20px;
  }
  .sidebar-contact-button {
    background-color: inherit;
    border: none;
    padding: 5px;
    border-radius: 5px;
    background-color: #35a07c;
  }
  .sidebar-contact-button:hover {
    background-color: #3cc094;
  }
  .logo {
    height: 35px;
    width: 35px;
  }
  .sidebar-fullname {
    text-align: center;
  }
}
@media (max-width: 1024px) {
  .sidebar {
    width: 100%;
    height: 100%;
    z-index: 1;
    background-color: rgba(0, 0, 0, 0.5);
    transition: width 0.5s;
  }
  .sidebar-content {
    background-color: #1f1f1f;
    height: auto;
    z-index: 2;
  }
  .sidebar-close-container {
    display: flex;
    justify-content: flex-end;
    width: 100%;
  }
  .sidebar-close-container > button {
    background-color: inherit;
    border: none;
    color: white;
  }
}
