@media (max-width: 1920px) {
  .contactme {
    width: 900px;
    margin-bottom: 50px;
  }
  .contact-form {
    color: white;
    padding: 10px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px;
    font-size: 15px;
  }
  .formhead {
    grid-column: 1/3;
  }
  input,
  textarea {
    background-color: inherit;
    outline: none;
    border: none;
    border-bottom: 1px solid #35a07c;
    padding: 5px;
    color: white;
    width: 80%;
  }
  input:focus {
    border-bottom: 1px solid white;
  }
  textarea {
    height: 100px;
    width: 100%;
  }
  input[type="file"] {
    width: 100%;
  }
  .form-submit-button {
    display: flex;
    justify-content: center;
  }
  .form-submit-button > input {
    width: 80px;
    padding: 10px;
    margin: 10px;
    border-radius: 20px;
    background-color: inherit;
    color: white;
    border: 1px solid #35a07c;
  }
  .form-submit-button > input:hover {
    background-color: #35a07c;
    border: none;
    color: black;
    cursor: pointer;
  }
  .form-attachment {
    color: white;
  }
}

@media (max-width: 1024px) {
  .contactme {
    width: 95%;
  }
}
