@media (max-width: 1920px) {
  .certifications {
    width: 900px;
  }
  .certi-grid {
    color: white;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px;
  }
  .certi-grid-category {
    grid-column: 1/3;
    text-align: center;
    border-bottom: 1px solid white;
    font-size: 30px;
    padding: 10px;
  }
  .certi-grid-head {
    font-size: 20px;
  }
  .certi-grid-details {
    font-size: 15px;
  }
}
@media (max-width: 1024px) {
  .certifications {
    width: 95%;
  }
  .certi-grid {
    font-size: 15px;
    grid-gap: 40px;
  }
  .certi-grid-category {
    font-size: 20px;
  }
  .certi-grid-details {
    font-size: 15px;
  }
}
