@media (max-width: 1920px) {
  .projects {
    width: 900px;
  }
  .project-flex-container {
    display: flex;
    justify-content: center;
  }
  .project-grid {
    width: 800px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
    padding: 20px 50px;
  }
  .block-half > a > div > img {
    width: 400px;
    height: 350px;
    border-radius: 20px;
    border: 1px solid white;
  }
  .project-grid > div {
    position: relative;
  }
  .block-background > div {
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 1;
  }
  .project-grid > div > a > div {
    color: white;
    text-align: center;
  }

  .grid-hide > div:nth-child(4n) {
    opacity: 0;
    transform: translate(-25%, 0);
    transition: transform 1s, opacity 1s;
  }
  .grid-hide > div:nth-child(4n + 1) {
    opacity: 0;
    transform: translate(25%, 0);
    transition: transform 1s, opacity 1s;
  }
  .grid-hide > div:nth-child(4n + 2) {
    opacity: 0;
    transform: translate(0, 25%);
    transition: transform 1s, opacity 1s;
  }
  .grid-hide > div:nth-child(4n + 3) {
    opacity: 0;
    transform: translate(-25%, 25%);
    transition: transform 1s, opacity 1s;
  }
  .project-animate > div:nth-child(4n) {
    opacity: 1;
    transform: translate(0, 0);
  }
  .project-animate > div:nth-child(4n + 1) {
    opacity: 1;
    transform: translate(0, 0);
  }
  .project-animate > div:nth-child(4n + 2) {
    opacity: 1;
    transform: translate(0, 0);
  }
  .project-animate > div:nth-child(4n + 3) {
    opacity: 1;
    transform: translate(0, 0);
  }
}
@media (max-width: 420px) {
  .projects {
    width: 90%;
  }
  .project-grid {
    width: 350px;
    padding: 20px 0px;
  }
  .block-half > a > div > img {
    width: 150px;
    height: 150px;
  }
}
@media (min-width: 420px) and (max-width: 1024px) {
  .projects {
    width: 90%;
  }
  .project-grid {
    width: 95%;
    padding: 20px 0px;
  }
  .block-half > a > div > img {
    width: 220px;
    height: 220px;
  }
}
@media (min-width: 420px) and (max-width: 520px) {
  .block-half > a > div > img {
    width: 200px;
    height: 200px;
  }
}
