@media (max-width: 1920px) {
  .navbar-container {
    position: fixed;
    display: flex;
    width: 900px;
    justify-content: space-around;
    align-items: center;
    padding: 10px 0px;
    z-index: 2;
  }
  .navbar {
    z-index: 1;
    width: 80%;
    border: 1px solid #6e6e6e;
    border-radius: 50px;
    padding: 5px;
    overflow: scroll;
    background-color: #181818;
    display: flex;
    justify-content: space-between;
    padding: 5px 30px;
    box-sizing: border-box;
  }
  .navbar > img {
    width: 25px;
    height: 25px;
  }
  .navbar::-webkit-scrollbar {
    display: none;
  }
}
@media (max-width: 1024px) {
  .navbar-container {
    width: 100%;
  }
  .navbar {
    width: 80%;
  }
  .navbar > img {
    width: 20px;
    height: 20px;
  }
}
