@media (max-width: 1920px) {
  .skill {
    width: 1000px;
  }
  .page-heading {
    font-size: 35px;
    color: white;
  }
  .skills-contianer {
    display: grid;
    padding: 40px;
    grid-template-columns: repeat(5, 1fr);
    grid-row-gap: 20px;
  }
  .skill-item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 35px 0px;
    color: white;
    border: 1px solid white;
    border-radius: 50px;
    box-sizing: border-box;
    max-width: 150px;
    font-size: 20px;
  }
  .skill-item > div > img {
    width: 50px;
    height: 50px;
  }
  .skill-item > div {
    margin-top: 5px;
    box-sizing: border-box;
  }
  .skill-item:hover {
    border-color: #35a07c;
  }
  .slide-up {
    opacity: 1;
    transform: translateY(0);
  }
  .skills-contianer > div:nth-child(5n + 1) {
    transform: translate(-25%, 0);
    opacity: 0;
  }
  .skills-contianer > div:nth-child(5n + 2) {
    transform: translate(0, 25%);
    opacity: 0;
  }
  .skills-contianer > div:nth-child(5n + 3) {
    transform: translate(25%, 25%);
    opacity: 0;
  }
  .skills-contianer > div:nth-child(5n + 4) {
    transform: translate(0, -25%);
    opacity: 0;
  }
  .skills-contianer > div:nth-child(5n + 5) {
    transform: translate(25%, 25%);
    opacity: 0;
  }
  .skills-contianer-animate > div:nth-child(5n + 5) {
    transition: transform 1.5s, opacity 1s;
    transform: translate(0, 0);
    opacity: 1;
  }
  .skills-contianer-animate > div:nth-child(5n + 4) {
    transition: transform 1.5s, opacity 1s;
    transform: translate(0, 0);
    opacity: 1;
  }
  .skills-contianer-animate > div:nth-child(5n + 3) {
    transition: transform 1.5s, opacity 1s;
    transform: translate(0, 0);
    opacity: 1;
  }
  .skills-contianer-animate > div:nth-child(5n + 2) {
    transition: transform 1.5s, opacity 1s;
    transform: translate(0, 0);
    opacity: 1;
  }
  .skills-contianer-animate > div:nth-child(5n + 1) {
    transition: transform 1.5s, opacity 1s;
    transform: translate(0, 0);
    opacity: 1;
  }
}
@media (max-width: 420px) {
  .skill {
    width: 90%;
  }
  .skills-contianer {
    padding: 5px;
    box-sizing: border-box;
    grid-gap: 15px;
    grid-template-columns: repeat(2, 1fr);
  }
  .skill-item > div > img {
    width: 30px;
    height: 30px;
  }
}
@media (min-width: 420px) and (max-width: 1024px) {
  .skill {
    width: 95%;
  }
  .skills-contianer {
    padding: 5px;
    box-sizing: border-box;
    grid-gap: 15px;
    grid-template-columns: repeat(4, 1fr);
  }
  .skill-item > div > img {
    width: 30px;
    height: 30px;
  }
}
